body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #D7DDD0 no-repeat center center fixed;
  background-size: cover;
  overflow: auto;
}
     
.container {
    text-align: center;
    margin-left: 23%;
    margin-right: 23%;
    
}

.card-heading {
    background-color: seagreen;
    padding: 5%;
    color: white;
}

.button-line {
    color:white;
}

.raised-button {
    box-shadow: 3px 30px 5px 2px rgba(255, 105, 135, .8);
    background-color: lime;
}
p {
    font-size: 20px;
    font-weight: 400;
    font-family: 'Darker Grotesque', sans-serif;
}
.appBar {
    height: 60px;
    padding-top: 10px;
    padding-bottom: 10px;
}

.navTitle {
    font-family: "Constantia";
    font-weight: 800;
    font-size: 30pt;
    float: left;
    margin-left: 5%;
    color: rgb(34, 68, 48);
}

.navButton {
    font-family: 'Darker grotesque', sans-serif;
    font-weight: 400;
    font-size: 16pt;
    float: right;
    margin-right: 5%;
    margin-top: 15px;
    bottom: 0px;
    flex-wrap: wrap;
    color:  rgb(17, 0, 255);
    text-decoration: none;
}

.spacer {
    height: 20px;
    width: 100%;
    background-color: blue;
}
.navButton:hover{
    cursor: pointer;
    color: black;
}
.link:hover{
    cursor: pointer;
    color: rgb(102, 3, 3);
}

@media only screen and (max-width: 700px) {
    .navTitle {
        font-size: 22pt;
    }
}

@media only screen and (max-width: 430px) {
    .navTitle {
        font-size: 12pt;
    }
}

@media only screen and (max-width: 700px) {
    .navButton {
        font-size: 12pt;
        margin-top: 8px;
    }
}

@media only screen and (max-width: 488px) {
    .navButton {
        font-size: 8pt;
    }
}

@media only screen and (max-width: 700px) {
    .appBar {
        height: 40px;
    }
}
